<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="shandui">
          <p>汇率：<span>655656</span></p>
          <yd-flexbox direction="vertical">
            <yd-flexbox-item class="list_input">
                <yd-flexbox>
                    <yd-flexbox-item>
                      <div class="selectli">
                        <span class="contitle" @click="seletitem('left')">{{leftcontent}}</span>
                        <input type="hidden" value="" v-model="leftcontent">
                        <ul class="seletitem" v-if="left">
                          <li @click="choosecon('ETH','left')">ETH</li>
                          <li @click="choosecon('BTC','left')">BTC</li>
                        </ul>
                      </div>
                    </yd-flexbox-item>
                    <div class="zhuanhua"><img src="../assets/images/zh.png"></div>
                    <yd-flexbox-item>
                      <div class="selectli">
                        <span class="contitle" @click="seletitem('right')">{{rightcontent}}</span>
                        <input type="hidden" value="" v-model="rightcontent">
                        <ul class="seletitem" v-if="right">
                          <li @click="choosecon('BDK','right')">BDK</li>
                          <li @click="choosecon('EOS','right')">EOS</li>
                        </ul>
                      </div>
                    </yd-flexbox-item>
                </yd-flexbox>
            </yd-flexbox-item>
            <yd-flexbox-item  class="list_input">
                <yd-flexbox>
                  <yd-flexbox-item>
                    <input type="number" class="con_input" placeholder="输入兑换数字">
                  </yd-flexbox-item>
                  <div class="zhuanhua"></div>
                  <yd-flexbox-item>
                    <input type="number" class="con_input" placeholder="0.0000">
                  </yd-flexbox-item>
              </yd-flexbox>
            </yd-flexbox-item>
        </yd-flexbox>
        <button class="shandui_btn">快速兑换</button>
        </div>
        <div class="dhjl_list">
          <p class="dhjl">兑换记录</p>
        </div>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
export default {
  components:{
      "headerBar":headerBar
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:'闪兑',
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      left:false,
      leftcontent:'ETH',
      right:false,
      rightcontent:"BDK",
    }
  },
  created() {
    // 点击其他不在的区域触发事件
    document.addEventListener('click', (e) => {
      //console.log(this.$el.contains(e.target));
      if (!this.$el.contains(e.target)){
          this.right = false; 
          this.left = false
      }
    })
  },
  methods:{
    //显示下拉
    seletitem(obj){
      if (obj == "left") {
         this.left = true
      }else{
        this.right = true
      }
    },
    //选择内容
    choosecon(obj,der){
      if (der == "left") {
        this.leftcontent = obj
        this.left = false
      }else{
        this.rightcontent = obj
        this.right = false
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.yingyong{
  padding-top: .9rem
}
.shandui{
  text-align: center;
  border-radius: 5px;
  border: 1px solid #f1f1f1;
  margin: .4rem;
  padding: .4rem
}
.shandui p{
  font-size: .28rem;
  color: #666;
}
.shandui p span{
  color: #e09a41
}
.contitle{
  text-align: left;
  padding-left: .5rem;
  width: 2.3rem;
  height: .68rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  display: block;
  line-height: .68rem;
  font-size: .3rem;
  background: url('../assets/images/sd.png') no-repeat 1.7rem center;
  background-size: .3rem .3rem;
}
.selectli{
  position: relative;
}
.seletitem{
  position: absolute;
  top: .68rem;
  left: 0;
  background: #fff;
  width: 2.3rem;
  border: 1px solid #eee;
  border-radius: 5px;
}
.seletitem li{
  text-align: left;
  display: block;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: .28rem;
  padding: .15rem .05rem .15rem .5rem;
}
.zhuanhua{
  width: .45rem;
  margin: 0 .35rem
}
.zhuanhua img{
  width: 90%
}
.con_input{
  text-align: left;
  width: 2.3rem;
  height: .68rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  display: block;
  line-height: .68rem;
  font-size: .28rem;
  padding: 0 .15rem;
  color: #888
}
.list_input{
  margin-top: .25rem
}
.shandui_btn{
  height: .7rem;
  line-height: .7rem;
  background: #278eed;
  color: #fff;
  border-radius: 4px;
  display: block;
  width: 100%;
  border: none;
  margin-top: .4rem;
  font-size: .3rem;
}
.shandui_btn:active{
  background: #357aba;
}
.dhjl{
  font-size: .3rem;
  color: #666;
}
.dhjl_list{
  padding: .4rem;
}
</style>



